.index{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    color: white;
    margin-top: -16px;
    width: 100%;
    height: 810px;
    background: linear-gradient(90deg, #237E91 -3.39%, #3E68BF 53.24%, #5C91D5 103.12%)
}
.index button{
    font-family: 'Montserrat', sans-serif;
    color: #FFFFFF;
    width: 40%;
    height: 50px;
    margin-top: 15px;
    gap: 10px;
    background: linear-gradient(90deg, #237E91 -3.39%, #3E68BF 53.24%, #5C91D5 103.12%);
    border: 3px solid #FFFFFF;
    box-shadow: 3px 4px 7px rgba(0, 0, 0, 0.25);
    border-radius: 37px;
    cursor: pointer;

}

.index button:hover {
    transform: scale(1.1);
    transition: all 0.5s;
  }